*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@font-face {
  font-family: 'Open Sans';
  src: url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');
}

body {
  font-family: 'Open Sans', sans-serif;
}
.mainContainer{
  display: flex;
  justify-content: center;
  max-width: 1250px;
  margin: 0 auto;
  height: 100vh;
}
span{
  font-family: 'Kumbh Sans';
font-style: normal;
font-weight: 400;
font-size: 15px;
line-height: 26px;
color: #69707D;
opacity: 1;
}
span:hover{
  cursor: pointer;
  color: #1D2026
}
.header{
  width: 100%;
  height: 120px;
  display: flex;
  border-bottom: 2px solid #E4E9F2;
}
.left-side{
  display: flex;
  width: 70%;
  align-items: center;
  justify-content: space-evenly;
}
.right-side{
  width: 30%;
  display: flex;
  gap: 46px;
  justify-content: flex-end;
  align-items: center;
}
.pfp{
  width: 50px;
  height: 50px;
margin-right: 70px;
}
.Collections{
  width: 90px;
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: solid 5px rgb(255, 96, 4,0);
    
}
.Collections:hover{
  border-bottom: solid 4px rgb(255, 96, 4,1);
}